import React, {useState} from "react";
import {SceneContainer} from "../components/SceneContainer";
import styled from "styled-components";
import {Button} from "../../common/button/Button";
import {UserService} from "../service/UserService";
import { useHistory } from "react-router-dom";

export interface Props {
    userService: UserService,
}

export function LoginPage(props: Props) {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const history = useHistory();

    return (
        <SceneContainer>
            <Form>
                <Title>Login / Register</Title>
                <EmailLabel>Email</EmailLabel>
                <Input value={email} onChange={(e) => setEmail(e.target.value)} type={'text'}/>
                <PasswordLabel>Password</PasswordLabel>
                <Input value={password} onChange={(e) => setPassword(e.target.value)} type={'password'}/>
                <ButtonRow><Button onClick={(e) => handleLogin(e)}>Login</Button></ButtonRow>
                <ButtonRow><Button onClick={(e) => handleRegister(e)}>Register</Button></ButtonRow>
            </Form>
        </SceneContainer>
    )

    function handleLogin(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        e.preventDefault();
        console.log(e.target);
        props.userService.login({username: email, password})
            .then(value => {
                console.log("logged in!");
                history.replace("/sessions");
            });
    }

    function handleRegister(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        e.preventDefault();
        console.log(e.target);
    }

}

const Form = styled.form`
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    align-self: center;
    justify-self: center;
`;

const Row = styled.div`
    margin-top: 10px;
    margin-bottom: 10px;
    width: 250px;
`;

const ButtonRow = styled(Row)`
   margin-top: 30px;
   display: flex;
   justify-content: center;
`;

const Title = styled(Row)`
    font-weight: 600;
    text-align: center;
    margin-bottom: 30px;
`;

const EmailLabel = styled(Row)`
    font-weight: 600;
`;

const PasswordLabel = styled(Row)`
    font-weight: 600;
`;

const Input = styled.input`
    font-weight: 600;
    width:100%;
`;