import * as React from "react";
import {StartSessionPage} from "./StartSessionPage";
import {SubjectService} from "../service/SubjectService";
import {SessionService} from "../service/SessionService";
import {UserService} from "../service/UserService";
import {PlaySessionPage} from "./PlaySessionPage";
import {EndSessionPage} from "./EndSessionPage";
import {BrowserRouter as Router, Route, Redirect, RouteProps, Switch} from "react-router-dom";
import {LoginPage} from "./LoginPage";
import {useEffect, useState} from "react";
import {LoadingPage} from "./LoadingPage";

type Props = {
    subjectService: SubjectService,
    sessionService: SessionService,
    userService: UserService,
}

export function GameManager(props: Props) {

    let {subjectService, sessionService} = props;

    return <Router>
        <Switch>
            <Route path={'/login'}>
                <LoginPage userService={props.userService}/>
            </Route>
            <PrivateRoute exact userService={props.userService} path={'/sessions'}>
                <StartSessionPage subjectService={subjectService}
                                  sessionService={sessionService}/>
            </PrivateRoute>
            <PrivateRoute exact userService={props.userService} path={'/sessions/:sessionId'}>
                <PlaySessionPage sessionService={sessionService}/>
            </PrivateRoute>
            <PrivateRoute exact userService={props.userService} path={'/sessions/:sessionId/results'}>
                <EndSessionPage sessionService={sessionService}/>
            </PrivateRoute>
            <PrivateRoute userService={props.userService} path={'/'}>
                <Redirect to={{pathname: "/sessions"}}/>
            </PrivateRoute>
        </Switch>
    </Router>;

}

type PrivateRouteProps = {
    userService: UserService,
} & RouteProps;

function PrivateRoute(props: PrivateRouteProps) {
    const {children, ...rest} = {...props};
    const [authenticating, setAuthenticating] = useState(true);
    const [isLoggedIn, setIsLoggedIn] = useState(true);

    useEffect(() => {
        props.userService.isAuthenticated()
            .then(value => {
                setIsLoggedIn(value);
                setAuthenticating(false);
            })
            .catch(reason => {
                setIsLoggedIn(false);
                setAuthenticating(false);
            });
    }, []);

    if (authenticating) {
        return <LoadingPage/>;
    }

    return (
        <Route {...rest} render={({location}) =>
            isLoggedIn ? (
                children
            ) : (
                <Redirect to={{
                    pathname: "/login",
                    state: {from: location}
                }}/>
            )
        }/>
    )
}
